@import url(https://fonts.googleapis.com/css2?family=Oswald:wght@400;500&display=swap);
@import url(https://fonts.googleapis.com/css2?family=Oxygen:wght@300;400&display=swap);
body {
  margin: 0;
}

code {
  font-family: source-code-pro, Menlo, Monaco, Consolas, 'Courier New',
    monospace;
}



html body {
  background-color: black;
  margin: 0 ;

}

body {
  font-family: 'Oxygen', sans-serif;
}

.header {
  color: #ffce00;
  font-family: 'Oswald', sans-serif;
  margin-left: 27%;
}

.navbar { padding: 2px !important }

.container {
    display: grid;
    grid-template-columns: 10% 35% 5% 5% 35% 10%;
    grid-template-rows: 50px 150px 25px 75px 100px 25px 75px 100px 25px 75px 100px 25px 125px 100px;
}

/* .filler {
  background-color: red;
  border: solid 1px white;
}

.item {
  background-color: green;
} */

.item-a {
    grid-column-start: 2;
    grid-column-end: 2;
    grid-row-start: 3;
    grid-row-end: span 2;
  }

.item-b {
    grid-column-start: 5;
    grid-column-end: 5;
    grid-row-start: 6;
    grid-row-end: span 2;
  }

  .item-c {
    grid-column-start: 2;
    grid-column-end: 2;
    grid-row-start: 9;
    grid-row-end: span 2;
  }

.item-d {
    grid-column-start: 5;
    grid-column-end: 5;
    grid-row-start: 12;
    grid-row-end: span 2;
  }

  .item-e {
    grid-column-start: 2;
    grid-column-end: span 4;
    grid-row-start: 15;
    grid-row-end: span 2;
    width: 400px;
    margin: 0 auto;
  }

.timelineHeader {
    grid-column-start: 2;
    grid-column-end: span 4;
    grid-row-start: 1;
    grid-row-end: 1;
    width: 400px;
    margin: 0 auto;
    text-align: center;
    /* border-bottom: dashed 2px white; */
    background-color: black;
    
  }

.locationDate {
  color: white;
}
.container2 {
    display: grid;
    grid-template-columns: 33% 33%;
    grid-template-rows: 350px 350px 350px;
    margin: 0 auto 0 27%
}

.container3 {
    display: grid;
    grid-template-columns: 35% 35%;
    grid-column-gap: 10px;
}
.skillsContainer {
    display: grid;
    grid-template-columns: 1fr 1fr 1fr;
    width: 46%;
    margin: 0 auto;
    grid-column-gap: 10px;
    -webkit-column-gap: 10px;
            column-gap: 10px;
}

.skills {
    border-radius: 5px;
    padding: 5px;
}

